<template>
  <div class="container">
    <div class="card register col-md-9 m-auto bg-gray-1">
      <div class="card-body">
        <div class="alert alert-warning" role="alert" v-if="this.error">{{this.error}}</div>
        <h1 class="text-center text-primary mt-3 mb-3">Đăng ký</h1>

        <div class="form-group">
          <label for="name">
            Your name
            <span class="required">(*)</span>
          </label>
          <input
            type="text"
            class="form-control"
            aria-describedby="emailHelp"
            placeholder="Enter name"
            autocomplete="off"
            v-model="name"
          />
        </div>
        <div class="form-group">
          <label for="email">
            Email address
            <span class="required">(*)</span>
          </label>
          <input
            type="email"
            class="form-control"
            aria-describedby="emailHelp"
            placeholder="Enter email"
            autocomplete="off"
            v-model="email"
          />
        </div>
        <div class="form-group">
          <label for="password1">
            Password
            <span class="required">(*)</span>
          </label>
          <input type="password" class="form-control" placeholder="Password" v-model="password" />
        </div>
        <div class="form-group">
          <label for="password2">
            Confirm password
            <span class="required">(*)</span>
          </label>
          <input
            type="password"
            class="form-control"
            aria-describedby="emailHelp"
            placeholder="Confirm password"
            v-model="repeatPassword"
          />
        </div>

        <button
          type="submit"
          class="btn btn-primary btn-block mt-4 mb-4"
          v-if="!isLoading"
          @click="register"
        >Register</button>
        <button class="buttonload btn btn-primary btn-block mt-4 mb-4" v-else>
          <i class="fa fa-spinner fa-spin mr-2"></i>Loading
        </button>
        <p class="mt-3">
          Đã có tài khoản?
          <router-link to="/login">Đăng nhập</router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  name: "Register",
  data() {
    return {
      name: "",
      email: "",
      password: "",
      repeatPassword: "",
      error: "",
    };
  },
  computed: {
    ...mapGetters(["isLoading"]),
  },
  methods: {
    async register() {
      this.$store.dispatch("setLoading", true);
      const response = await axios.post("/api/register", {
        name: this.name,
        password: this.password,
        repeatPassword: this.repeatPassword,
        email: this.email,
      });
      this.$store.dispatch("setLoading", false);
      if (response.data.isError) {
        this.error = response.data.error;
      } else {
        this.error = "";
        this.$router.push("/login?msg=registerSuccess");
      }
    },
  },
  beforeCreate() {
    this.forwardAuthenticate();
  },
};
</script>

<style>
.required {
  color: #e74c3c;
}
</style>